import Link from "next/link"
import Image from "next/image"
import ScrollReveal from './ScrollReveal'


const projects = [
  { slug: 'project-alpha', title: 'Project Alpha', img: '/images/project/alpha.jpg' },
  { slug: 'project-beta', title: 'Project Beta', img: '/images/project/beta.jpg' },
  { slug: 'project-gamma', title: 'Project Gamma', img: '/images/project/gamma.jpg' },
]

export default function ProjectsPreview() {
  return (
    <ScrollReveal className="py-20 bg-slate-50">
      <div className="container mx-auto">
        <h2 className="text-3xl font-semibold text-center mb-8">Selected Projects</h2>
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
          {projects.map((p, idx) => (
            <ScrollReveal key={p.slug} delay={idx * 0.08}>
              {/* existing card markup */}
            </ScrollReveal>
          ))}
        </div>
      </div>
    </ScrollReveal>
  )
}
